import { NextRequest, NextResponse } from "next/server";

/**
 * ÏãúÎìúÎ•º ÏÇ¨Ïö©Ìïú ÎûúÎç§ Îß§Î¨º Îç∞Ïù¥ÌÑ∞ ÏÉùÏÑ± Ìï®Ïàò
 * Îß§Î¨º Î™©Î°ù APIÏôÄ ÎèôÏùºÌïú Î°úÏßÅÏùÑ ÏÇ¨Ïö©ÌïòÏó¨ ÏùºÍ¥ÄÎêú Îç∞Ïù¥ÌÑ∞Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§.
 */
function generateRandomPropertyWithSeed(seed: string) {
  const locations = [
    "Í∞ïÎÇ®Íµ¨",
    "ÏÑúÏ¥àÍµ¨",
    "ÏÜ°ÌååÍµ¨",
    "ÎßàÌè¨Íµ¨",
    "Ïö©ÏÇ∞Íµ¨",
    "ÏÑ±ÎèôÍµ¨",
    "ÏòÅÎì±Ìè¨Íµ¨",
    "Í∞ïÎèôÍµ¨",
    "ÎÖ∏ÏõêÍµ¨",
    "Ï¢ÖÎ°úÍµ¨",
    "Ï§ëÍµ¨",
    "Í∞ïÏÑúÍµ¨",
    "ÎèôÏûëÍµ¨",
  ];

  const propertyTypes = [
    "ÏïÑÌååÌä∏",
    "Ïò§ÌîºÏä§ÌÖî",
    "ÎπåÎùº",
    "ÏÉÅÍ∞Ä",
    "Îã®ÎèÖÏ£ºÌÉù",
    "ÏõêÎ£∏",
  ];
  const riskTypes = ["LOW", "MEDIUM", "HIGH", "VERY_HIGH"];

  // ÏãúÎìú Í∏∞Î∞ò ÎûúÎç§ ÏÑ†ÌÉù (Îçî Îã§ÏñëÌïòÍ≤å)
  const seedHash = seed.split("").reduce((a, b) => {
    a = (a << 5) - a + b.charCodeAt(0);
    return a & a;
  }, 0);

  const locationIndex = Math.abs(seedHash) % locations.length;
  const propertyTypeIndex = Math.abs(seedHash >> 8) % propertyTypes.length;
  const riskTypeIndex = Math.abs(seedHash >> 16) % riskTypes.length;

  const location = locations[locationIndex];
  const propertyType = propertyTypes[propertyTypeIndex];
  const riskType = riskTypes[riskTypeIndex];

  // Îß§Î¨º Ïú†ÌòïÎ≥Ñ ÌäπÏÑ±Ïóê ÎßûÎäî Î©¥Ï†ÅÍ≥º Í∞ÄÍ≤© ÏÑ§Ï†ï
  let area, appraisedValue;

  switch (propertyType) {
    case "ÏïÑÌååÌä∏":
      area = 60 + (Math.abs(seedHash >> 24) % 90); // 60-150„é°
      appraisedValue = 300000000 + (Math.abs(seedHash >> 32) % 1200000000); // 3Ïñµ-15Ïñµ
      break;
    case "Ïò§ÌîºÏä§ÌÖî":
      area = 25 + (Math.abs(seedHash >> 24) % 50); // 25-75„é°
      appraisedValue = 150000000 + (Math.abs(seedHash >> 32) % 850000000); // 1.5Ïñµ-10Ïñµ
      break;
    case "ÎπåÎùº":
      area = 30 + (Math.abs(seedHash >> 24) % 70); // 30-100„é°
      appraisedValue = 200000000 + (Math.abs(seedHash >> 32) % 800000000); // 2Ïñµ-10Ïñµ
      break;
    case "ÏÉÅÍ∞Ä":
      area = 40 + (Math.abs(seedHash >> 24) % 110); // 40-150„é°
      appraisedValue = 100000000 + (Math.abs(seedHash >> 32) % 1400000000); // 1Ïñµ-15Ïñµ
      break;
    case "Îã®ÎèÖÏ£ºÌÉù":
      area = 80 + (Math.abs(seedHash >> 24) % 120); // 80-200„é°
      appraisedValue = 400000000 + (Math.abs(seedHash >> 32) % 1100000000); // 4Ïñµ-15Ïñµ
      break;
    case "ÏõêÎ£∏":
      area = 15 + (Math.abs(seedHash >> 24) % 35); // 15-50„é°
      appraisedValue = 80000000 + (Math.abs(seedHash >> 32) % 420000000); // 8Ï≤úÎßå-5Ïñµ
      break;
    default:
      area = 25 + (Math.abs(seedHash >> 24) % 125);
      appraisedValue = 50000000 + (Math.abs(seedHash >> 32) % 1450000000);
  }

  // ÏãúÏûë ÏûÖÏ∞∞Í∞Ä (Í∞êÏ†ïÍ∞ÄÏùò 70-85%)
  const startingBidRate = 0.7 + (Math.abs(seedHash >> 40) % 15) / 100;
  const startingBid = Math.floor(appraisedValue * startingBidRate);

  // ÏãúÏû•Í∞Ä (Í∞êÏ†ïÍ∞ÄÏôÄ Îã§Î•¥Í≤å ÏÑ§Ï†ï - 85-120% Î≤îÏúÑ)
  const marketPriceRate = 0.85 + (Math.abs(seedHash >> 48) % 35) / 100; // 85-120%
  const marketPrice = Math.floor(appraisedValue * marketPriceRate);

  // Îß§Î¨º Ïú†ÌòïÎ≥Ñ Ï†úÎ™© ÏÉùÏÑ± (Îçî Îã§ÏñëÌïòÍ≥† ÌòÑÏã§Ï†ÅÏù∏ Ïù¥Î¶Ñ)
  let title = "";

  switch (propertyType) {
    case "ÏïÑÌååÌä∏":
      const apartmentBrands = [
        "ÎûòÎØ∏Ïïà",
        "ÌûêÏä§ÌÖåÏù¥Ìä∏",
        "ÏïÑÌÅ¨Î°ú",
        "Ìó¨Î¶¨Ïò§ÏãúÌã∞",
        "ÎìúÎ¶ºÌå∞Î¶¨Ïä§",
        "ÎûòÎØ∏ÏïàÏïÑÌä∏Ìå∞Î¶¨Ïä§",
        "ÌÉÄÏõåÌå∞Î¶¨Ïä§",
        "ÏïÑÏù¥ÌååÌÅ¨",
        "ÏûêÏù¥",
        "ÏÑºÌä∏Îü¥ÌååÌÅ¨",
        "Ìä∏Î¶¨ÏßÄÏõÄ",
      ];
      const apartmentName =
        apartmentBrands[Math.abs(seedHash >> 56) % apartmentBrands.length];
      const buildingNum = Math.floor(Math.abs(seedHash >> 64) % 5) + 101; // 101-105Îèô
      const floorNum = Math.floor(Math.abs(seedHash >> 72) % 20) + 1; // 1-20Ï∏µ
      const roomNum = Math.floor(Math.abs(seedHash >> 80) % 4) + 1; // 1-4Ìò∏
      title = `${location} ${apartmentName} ${buildingNum}Îèô ${floorNum}${roomNum}Ìò∏`;
      break;
    case "Ïò§ÌîºÏä§ÌÖî":
      const officetelBrands = [
        "ÎπÑÏ¶àÎãàÏä§ÌÖî",
        "Ïä§ÎßàÌä∏Ïò§ÌîºÏä§ÌÖî",
        "ÎπÑÏ¶àÎãàÏä§ÏÑºÌÑ∞",
        "Ïò§ÌîºÏä§ÌÖî",
        "ÎπÑÏ¶àÎãàÏä§ÎπåÎî©",
        "Ïä§ÎßàÌä∏ÎπåÎî©",
      ];
      const officetelName =
        officetelBrands[Math.abs(seedHash >> 56) % officetelBrands.length];
      const officetelFloor = Math.floor(Math.abs(seedHash >> 64) % 10) + 1; // 1-10Ï∏µ
      const officetelRoom = Math.floor(Math.abs(seedHash >> 72) % 20) + 1; // 1-20Ìò∏
      title = `${location} ${officetelName} ${officetelFloor}Ï∏µ ${officetelRoom}Ìò∏`;
      break;
    case "ÎπåÎùº":
      const villaNames = [
        "ÎπåÎùº",
        "Îã§ÏÑ∏ÎåÄÏ£ºÌÉù",
        "Ïó∞Î¶ΩÏ£ºÌÉù",
        "Îã§Í∞ÄÍµ¨Ï£ºÌÉù",
        "ÎπåÎî©",
        "Ï£ºÌÉù",
      ];
      const villaName =
        villaNames[Math.abs(seedHash >> 56) % villaNames.length];
      const villaFloor = Math.floor(Math.abs(seedHash >> 64) % 4) + 1; // 1-4Ï∏µ
      const villaRoom = Math.floor(Math.abs(seedHash >> 72) % 10) + 1; // 1-10Ìò∏
      title = `${location} ${villaName} ${villaFloor}Ï∏µ ${villaRoom}Ìò∏`;
      break;
    case "ÏÉÅÍ∞Ä":
      const commercialNames = [
        "ÏÉÅÍ∞Ä",
        "ÏÉÅÍ∞ÄÍ±¥Î¨º",
        "ÏÉÅÏóÖÏãúÏÑ§",
        "ÏÉÅÍ∞ÄÎèô",
        "ÏÉÅÍ∞ÄÎπåÎî©",
        "ÏÉÅÍ∞ÄÍ±¥Î¨º",
      ];
      const commercialName =
        commercialNames[Math.abs(seedHash >> 56) % commercialNames.length];
      const commercialFloor = Math.floor(Math.abs(seedHash >> 64) % 5) + 1; // 1-5Ï∏µ
      const commercialRoom = Math.floor(Math.abs(seedHash >> 72) % 20) + 1; // 1-20Ìò∏
      title = `${location} ${commercialName} ${commercialFloor}Ï∏µ ${commercialRoom}Ìò∏`;
      break;
    case "Îã®ÎèÖÏ£ºÌÉù":
      const houseNames = [
        "Îã®ÎèÖÏ£ºÌÉù",
        "Ï£ºÌÉù",
        "ÌïúÏò•",
        "Îã®ÎèÖÏ£ºÌÉù",
        "Ï£ºÌÉù",
        "Îã®ÎèÖÏ£ºÌÉù",
      ];
      const houseName =
        houseNames[Math.abs(seedHash >> 56) % houseNames.length];
      title = `${location} ${houseName} ${area}„é°`;
      break;
    case "ÏõêÎ£∏":
      const studioNames = [
        "ÏõêÎ£∏",
        "Ïä§ÌäúÎîîÏò§",
        "ÏõêÎ£∏",
        "Ïä§ÌäúÎîîÏò§",
        "ÏõêÎ£∏",
        "Ïä§ÌäúÎîîÏò§",
      ];
      const studioName =
        studioNames[Math.abs(seedHash >> 56) % studioNames.length];
      const studioFloor = Math.floor(Math.abs(seedHash >> 64) % 10) + 1; // 1-10Ï∏µ
      const studioRoom = Math.floor(Math.abs(seedHash >> 72) % 20) + 1; // 1-20Ìò∏
      title = `${location} ${studioName} ${studioFloor}Ï∏µ ${studioRoom}Ìò∏`;
      break;
    default:
      title = `${location} ${propertyType} ${area}„é°`;
  }

  // ÏãúÎìú Í∏∞Î∞ò Ïù¥ÎØ∏ÏßÄ URL ÏÉùÏÑ± (ÏùºÍ¥ÄÏÑ± Ïú†ÏßÄ)
  const imageSeed = Math.abs(
    seed.split("").reduce((a, b) => a + b.charCodeAt(0), 0)
  );
  const imageUrl = `https://images.unsplash.com/photo-${imageSeed}?w=400&h=300&fit=crop`;

  // ÏãúÎìú Í∏∞Î∞ò Ï∂îÍ∞Ä Îç∞Ïù¥ÌÑ∞ ÏÉùÏÑ±
  const riskData = {
    legalIssues: Math.abs(seedHash >> 88) % 2 === 0,
    marketVolatility: Math.abs(seedHash >> 96) % 3,
    competitionLevel: Math.abs(seedHash >> 104) % 3,
  };

  const auctionItem = {
    id: seed,
    title,
    itemType: propertyType,
    imageUrl,
    appraisedValue,
    startingBid,
    marketPrice,
    riskType,
    riskData,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
  };

  console.log(`‚úÖ Îß§Î¨º Îç∞Ïù¥ÌÑ∞ ÏÉùÏÑ± ÏôÑÎ£å: ${title}`);
  console.log(
    `üí∞ Í∞ÄÍ≤© Ï†ïÎ≥¥: Í∞êÏ†ïÍ∞Ä ${appraisedValue}Ïõê, ÏãúÏû•Í∞Ä ${marketPrice}Ïõê, ÏãúÏûëÏûÖÏ∞∞Í∞Ä ${startingBid}Ïõê`
  );

  return auctionItem;
}

/**
 * Îß§Î¨º IDÎ°ú Îß§Î¨º Ï†ïÎ≥¥Î•º Ï°∞ÌöåÌïòÎäî Ìï®Ïàò
 * Í∏∞Ï°¥ API Î°úÏßÅÏùÑ Ïû¨ÏÇ¨Ïö©ÌïòÏó¨ ÏùºÍ¥ÄÎêú Îç∞Ïù¥ÌÑ∞Î•º Î∞òÌôòÌï©ÎãàÎã§.
 */
async function getAuctionItemById(id: string) {
  try {
    console.log(`üîç Îß§Î¨º Ï†ïÎ≥¥ Ï°∞Ìöå ÏãúÏûë - ID: ${id}`);

    // Îß§Î¨º IDÎ•º Í∏∞Î∞òÏúºÎ°ú ÎûúÎç§ Îç∞Ïù¥ÌÑ∞ ÏÉùÏÑ± (ÏùºÍ¥ÄÏÑ±ÏùÑ ÏúÑÌï¥ ÏãúÎìú ÏÇ¨Ïö©)
    let seed = id;
    if (id.includes("_")) {
      // Ïñ∏ÎçîÏä§ÏΩîÏñ¥Í∞Ä ÏûàÎäî Í≤ΩÏö∞ ÎßàÏßÄÎßâ Î∂ÄÎ∂ÑÏùÑ ÏãúÎìúÎ°ú ÏÇ¨Ïö©
      const parts = id.split("_");
      seed = parts[parts.length - 1] || id;
    }

    console.log(`üîç ÏÇ¨Ïö©Ìï† ÏãúÎìú: ${seed}`);

    // ÏãúÎìú Í∏∞Î∞ò Îß§Î¨º Îç∞Ïù¥ÌÑ∞ ÏÉùÏÑ±
    const auctionItem = generateRandomPropertyWithSeed(seed);

    console.log(`‚úÖ Îß§Î¨º Ï†ïÎ≥¥ Ï°∞Ìöå ÏôÑÎ£å: ${auctionItem.title}`);
    return auctionItem;
  } catch (error) {
    console.error("‚ùå Îß§Î¨º Ï†ïÎ≥¥ Ï°∞Ìöå Ïã§Ìå®:", error);
    return null;
  }
}

/**
 * ÏûÖÏ∞∞ Ï≤òÎ¶¨ API
 * POST /api/auctions/[id]/bid
 *
 * ÏÇ¨Ïö©ÏûêÏùò ÏûÖÏ∞∞ÏùÑ Ï≤òÎ¶¨ÌïòÍ≥† ÏãúÎÆ¨Î†àÏù¥ÏÖò Í≤∞Í≥ºÎ•º Í≥ÑÏÇ∞Ìï©ÎãàÎã§.
 * ÏöîÏ≤≠ Î≥∏Î¨∏: { userId: string, bidAmount: number }
 */
export async function POST(
  request: NextRequest,
  { params }: { params: Promise<{ id: string }> }
) {
  try {
    const resolvedParams = await params;
    const auctionItemId = resolvedParams.id;
    console.log(`üéØ ÏûÖÏ∞∞ Ï≤òÎ¶¨ ÏãúÏûë - Îß§Î¨º ID: ${auctionItemId}`);

    // ÏöîÏ≤≠ Î≥∏Î¨∏ ÌååÏã±
    const body = await request.json();
    const { bidAmount } = body;

    // ÏûÖÎ†•Í∞í Í≤ÄÏ¶ù
    if (!bidAmount || bidAmount <= 0) {
      console.log("‚ùå ÏûòÎ™ªÎêú ÏûÖÎ†•Í∞í:", { bidAmount });
      return NextResponse.json(
        {
          success: false,
          error: "ÏûÖÏ∞∞Í∞ÄÎ•º Ïò¨Î∞îÎ•¥Í≤å ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
        },
        { status: 400 }
      );
    }

    console.log(`üí∞ ÏûÖÏ∞∞Í∞Ä: ${bidAmount}Ïõê`);

    // Ïã§Ï†ú Îß§Î¨º Îç∞Ïù¥ÌÑ∞ Ï°∞Ìöå (Í∏∞Ï°¥ API Î°úÏßÅ Ïû¨ÏÇ¨Ïö©)
    const auctionItem = await getAuctionItemById(auctionItemId);

    if (!auctionItem) {
      console.log("‚ùå Îß§Î¨ºÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§:", auctionItemId);
      return NextResponse.json(
        {
          success: false,
          error: "Îß§Î¨ºÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§",
        },
        { status: 404 }
      );
    }

    console.log(
      `üì¶ Îß§Î¨º Ï†ïÎ≥¥: ${auctionItem.title}, Í∞êÏ†ïÍ∞Ä: ${auctionItem.appraisedValue}Ïõê, ÏãúÏû•Í∞Ä: ${auctionItem.marketPrice}Ïõê`
    );
    console.log(`üîç Îß§Î¨º ÏÉÅÏÑ∏ Ï†ïÎ≥¥:`, {
      id: auctionItem.id,
      title: auctionItem.title,
      appraisedValue: auctionItem.appraisedValue,
      marketPrice: auctionItem.marketPrice,
      startingBid: auctionItem.startingBid,
      riskType: auctionItem.riskType,
    });

    // ÏãúÎÆ¨Î†àÏù¥ÏÖò Í≤∞Í≥º Í≥ÑÏÇ∞
    console.log(
      `üßÆ ÏãúÎÆ¨Î†àÏù¥ÏÖò Í≥ÑÏÇ∞ ÏãúÏûë - ÏûÖÏ∞∞Í∞Ä: ${bidAmount}Ïõê, Í∞êÏ†ïÍ∞Ä: ${auctionItem.appraisedValue}Ïõê, ÏãúÏû•Í∞Ä: ${auctionItem.marketPrice}Ïõê`
    );
    const simulationResult = calculateSimulationResult(auctionItem, bidAmount);
    console.log(`üìä ÏãúÎÆ¨Î†àÏù¥ÏÖò Í≤∞Í≥º:`, simulationResult);
    console.log(`üí∞ ÏµúÏ¢Ö ÏàòÏùµ/ÏÜêÏã§: ${simulationResult.profitOrLoss}Ïõê`);

    // ÏÑ±Í≥µ ÏùëÎãµ Î∞òÌôò - ÏõêÎûò Îß§Î¨ºÏùò Í∞ÄÍ≤© Ï†ïÎ≥¥Î•º Í∑∏ÎåÄÎ°ú ÏÇ¨Ïö©
    const responseData = {
      success: true,
      finalBid: bidAmount,
      profitOrLoss: simulationResult.profitOrLoss,
      marketPrice: auctionItem.marketPrice, // ÏõêÎûò Îß§Î¨ºÏùò ÏãúÏû•Í∞Ä ÏÇ¨Ïö©
      appraisedValue: auctionItem.appraisedValue, // ÏõêÎûò Îß§Î¨ºÏùò Í∞êÏ†ïÍ∞Ä ÏÇ¨Ïö©
      riskLevel: auctionItem.riskType,
      recommendation: simulationResult.recommendation,
      details: {
        competitionLevel: "Î≥¥ÌÜµ",
        biddingHistory: [
          { bid: bidAmount - 100000, timestamp: new Date().toISOString() },
          { bid: bidAmount, timestamp: new Date().toISOString() },
        ],
        marketTrend: "ÏïàÏ†ïÏ†Å",
      },
      message: "ÏûÖÏ∞∞ Ï≤òÎ¶¨Í∞Ä ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§",
    };

    console.log(`‚úÖ ÏûÖÏ∞∞ ÏùëÎãµ Îç∞Ïù¥ÌÑ∞:`, {
      finalBid: responseData.finalBid,
      profitOrLoss: responseData.profitOrLoss,
      marketPrice: responseData.marketPrice,
      appraisedValue: responseData.appraisedValue,
      riskLevel: responseData.riskLevel,
      originalMarketPrice: auctionItem.marketPrice,
      originalAppraisedValue: auctionItem.appraisedValue,
      priceConsistency: {
        marketPriceMatch: responseData.marketPrice === auctionItem.marketPrice,
        appraisedValueMatch:
          responseData.appraisedValue === auctionItem.appraisedValue,
      },
    });

    return NextResponse.json(responseData, { status: 200 });
  } catch (error) {
    console.error("‚ùå ÏûÖÏ∞∞ Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);

    return NextResponse.json(
      {
        success: false,
        error: "ÏûÖÏ∞∞ Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§",
        details: error instanceof Error ? error.message : "Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò",
      },
      { status: 500 }
    );
  }
}

/**
 * ÏãúÎÆ¨Î†àÏù¥ÏÖò Í≤∞Í≥º Í≥ÑÏÇ∞ Ìï®Ïàò
 * Îß§Î¨ºÏùò ÏúÑÌóòÎèÑÏôÄ ÏûÖÏ∞∞Í∞ÄÎ•º Í∏∞Î∞òÏúºÎ°ú Í≤∞Í≥ºÎ•º Í≥ÑÏÇ∞Ìï©ÎãàÎã§.
 */
function calculateSimulationResult(auctionItem: any, bidAmount: number) {
  const { appraisedValue, marketPrice, riskType } = auctionItem;

  console.log(
    `üîç ÏãúÎÆ¨Î†àÏù¥ÏÖò Í≥ÑÏÇ∞ ÏãúÏûë - ÏûÖÏ∞∞Í∞Ä: ${bidAmount}Ïõê, Í∞êÏ†ïÍ∞Ä: ${appraisedValue}Ïõê, ÏãúÏû•Í∞Ä: ${marketPrice}Ïõê`
  );

  // ÏõêÎûò ÏãúÏû•Í∞ÄÏôÄ Í∞êÏ†ïÍ∞ÄÎ•º Ïú†ÏßÄ (Ï°∞Ï†ïÌïòÏßÄ ÏïäÏùå)
  console.log(
    `üìä ÏõêÎûò Í∞ÄÍ≤© Ïú†ÏßÄ - ÏãúÏû•Í∞Ä: ${marketPrice}Ïõê, Í∞êÏ†ïÍ∞Ä: ${appraisedValue}Ïõê`
  );

  // Í∏∞Î≥∏ ÌôïÎ•† Í≥ÑÏÇ∞ (Í∞êÏ†ïÍ∞Ä ÎåÄÎπÑ ÏûÖÏ∞∞Í∞Ä ÎπÑÏú®)
  const bidRatio = bidAmount / appraisedValue;

  // ÏúÑÌóòÎèÑÏóê Îî∞Î•∏ ÏÑ±Í≥µ ÌôïÎ•† Ï°∞Ï†ï
  let successProbability = 0.7; // Í∏∞Î≥∏ ÏÑ±Í≥µ ÌôïÎ•† 70%

  switch (riskType) {
    case "LOW":
      successProbability = 0.9; // ÎÇÆÏùÄ ÏúÑÌóò: 90%
      break;
    case "MEDIUM":
      successProbability = 0.7; // Ï§ëÍ∞Ñ ÏúÑÌóò: 70%
      break;
    case "HIGH":
      successProbability = 0.4; // ÎÜíÏùÄ ÏúÑÌóò: 40%
      break;
    case "VERY_HIGH":
      successProbability = 0.2; // Îß§Ïö∞ ÎÜíÏùÄ ÏúÑÌóò: 20%
      break;
  }

  // ÏûÖÏ∞∞Í∞ÄÍ∞Ä Í∞êÏ†ïÍ∞ÄÎ≥¥Îã§ ÎÜíÏúºÎ©¥ ÏÑ±Í≥µ ÌôïÎ•† Í∞êÏÜå
  if (bidAmount > appraisedValue) {
    successProbability *= 0.5;
  }

  // ÎûúÎç§ Í≤∞Í≥º ÏÉùÏÑ±
  const randomValue = Math.random();
  const isSuccess = randomValue < successProbability;

  let profitOrLoss = 0;
  let recommendation = "";

  if (isSuccess) {
    // ÏÑ±Í≥µÌïú Í≤ΩÏö∞
    // ÏàòÏùµ Í≥ÑÏÇ∞ (ÏõêÎûò ÏãúÏû•Í∞Ä - ÏûÖÏ∞∞Í∞Ä)
    profitOrLoss = marketPrice - bidAmount;

    if (profitOrLoss > 0) {
      // Ïù¥ÏùµÏù∏ Í≤ΩÏö∞
      recommendation = "üéâ Ï∂ïÌïòÌï©ÎãàÎã§! ÏàòÏùµÏùÑ Í±∞ÎëêÏóàÏäµÎãàÎã§.";
      console.log(`üí∞ Ïù¥Ïùµ Î∞úÏÉù: ${profitOrLoss}Ïõê`);
    } else {
      // ÏÜêÏã§Ïù∏ Í≤ΩÏö∞
      recommendation = "‚ö†Ô∏è ÏûÖÏ∞∞Í∞ÄÍ∞Ä ÏãúÏû•Í∞ÄÎ≥¥Îã§ ÎÜíÏïÑ ÏÜêÏã§Ïù¥ Î∞úÏÉùÌñàÏäµÎãàÎã§.";
      console.log(`üí∏ ÏÜêÏã§ Î∞úÏÉù: ${Math.abs(profitOrLoss)}Ïõê`);
    }
  } else {
    // Ïã§Ìå®Ìïú Í≤ΩÏö∞
    profitOrLoss = -bidAmount; // ÏûÖÏ∞∞Í∞ÄÎßåÌÅº ÏÜêÏã§
    recommendation = "‚ùå ÏûÖÏ∞∞Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïùå Í∏∞ÌöåÎ•º ÎÖ∏Î†§Î≥¥ÏÑ∏Ïöî.";
    console.log(`‚ùå ÏûÖÏ∞∞ Ïã§Ìå®: ${bidAmount}Ïõê ÏÜêÏã§`);
  }

  // ÏãúÎÆ¨Î†àÏù¥ÏÖò Í≤∞Í≥º Î∞òÌôò
  return {
    profitOrLoss,
    recommendation,
    successProbability: Math.round(successProbability * 100), // Î∞±Î∂ÑÏú®Î°ú Î∞òÌôò
  };
}
